.title {
  font-weight: 700;
  margin-bottom: 5px;
  font-size: 25px;
  color: #727272;
}
.title:not(:first-child) {
  margin-top: 30px;
}
.scrollbar-container {
  width: 100%;
}
.FolderIcon {
  width: 70px;
  height: 70px;
  background-color: #0097f7;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  position: relative;
  margin-bottom: 10px;
  margin-top: 20px;
}
.group {
  background-color: #c26421;
}
.registration {
  background-color: #456d85;
}
.voluneer {
  background-color: #cea64c;
}
.language {
  background-color: #956d4e;
}

@media screen and (max-width: 761px) {
  .FolderIcon {
    margin:auto;
  }
}