.menu-active {
  background-color: rgba(47, 66, 80, 0.2);
}
@media only screen and (max-width: 360px) {
  /* For mobile phones: */
  [class*="col-"] {
    width: 100%;
  }
}
.icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .col-3 {
    display: none;
  }
  .icon {
    float: left;
    display: block;
  }
}

@media screen and (min-width: 1061px) {
  .hide-mobile {
    display: none;
  }

}